@import url("https://fonts.googleapis.com/css2?family=Fira+Sans:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&family=Unbounded:wght@200..900&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Inter:ital,opsz,wght@0,14..32,100..900;1,14..32,100..900&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
:root {
  --blue: #c5fcfc;
  --bg: #121315;
  --textColor: #e0eeee;
  --font: "Fira Sans", sans-serif;
  --titleFont: "Unbounded", sans-serif;
  --selectionColor: #a9e4a3;
  --green: #d7fc01;
  --inter: "Inter", sans-serif;
}
::selection {
  color: var(--green);
  background: transparent;
}
body {
  background-color: var(--bg);
}
main {
  width: 88%;
  margin: 0 auto;
  position: relative;
}
.header-overlay {
  position: absolute;
  display: block;
  margin: 0 auto;
  z-index: -1;
  /* background: var(--bg); */
  width: 100%;
  height: 100%;
}
header {
  position: fixed;
  height: 8vh;
  width: 88%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  z-index: 999;
}
header img {
  width: 200px;
}
.hero {
  position: relative;
  height: 100vh;
  width: 100%;
  z-index: 11;
  /* overflow: hidden; */
}
.hero-content {
  position: relative;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  text-align: center;
}
.hero-primary-text h1:first-child {
  font-size: 5rem;
}
.hero-primary-text h1:nth-child(2) {
  font-size: 2.95rem;
}
.hero-primary-text {
  text-transform: uppercase;
  color: var(--textColor);
  text-wrap: nowrap;
  line-height: 55px;
  font-family: var(--titleFont);
  letter-spacing: -2px;
}
.hero-primary-text span {
  color: var(--green);
}
.hero-secondary-text {
  font-family: var(--font);
  text-transform: uppercase;
  font-size: 0.9rem;
  color: #e0eeeee3;
  margin-top: 15px;
  font-weight: 400;
}
.hero-arrow {
  margin-top: 40px;
  color: var(--textColor);
  font-size: 1.6rem;
}
.hero-arrow span {
  cursor: pointer;
}

.hero-bottom {
  height: 14.2vh;
  width: 100%;
  position: absolute;
  bottom: 0;
  left: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-family: var(--font);
}
.hero-bottom span {
  border: 1px solid var(--green);
  padding: 1rem;
  border-radius: 9999px;
  color: var(--green);
  cursor: pointer;
  font-size: 1rem;
}
.hero-socials-icon {
  font-size: 1.8rem;
}
.hero-socials {
  display: flex;
  width: 17%;
  justify-content: space-between;
}

.video-section {
  position: relative;
  width: 100%;
  min-height: 440vh;
  /* height: 250vh; */
}
.video-container {
  display: block;
  margin: 0 auto;
  position: sticky;
  top: 3vh;
  width: 100%;
}
.video-container-inner {
  width: 100%;
  height: 100%;
  position: relative;
}
.video-section video {
  width: 100%;
  height: 94vh;
  object-fit: cover;
}
.video-text {
  position: absolute;
  /* width: 600px; */
  z-index: 55;
  display: flex;
  flex-direction: column;
  font-family: var(--font);
  text-transform: uppercase;
  font-size: 1.5rem;
  color: var(--textColor);
  /* overflow: hidden; */
}
.video-text span:nth-child(2) {
  margin-top: 10px;
}
.video-text:nth-child(1) {
  top: 10vh;
  left: 4%;
}
.video-text:nth-child(2) {
  top: 38vh;
  left: 4%;
}
.video-text:nth-child(3) {
  top: 65vh;
  left: 4%;
}
.video-text .title {
  font-size: 6rem;
  font-weight: 400;
}
.video-breakpoints-container {
  position: absolute;
  visibility: hidden;
  /* opacity: 0; */
  display: flex;
  top: 110vh;
  z-index: 90;
  flex-direction: column;
  /* align-items: center; */
  /* justify-content: center; */
  background: orange;
  overflow: hidden;
}
.video-breakpoints-container div {
  margin-top: 100vh;
}
.video-breakpoints-container div:first-child {
  margin-top: 0;
}

.scroll-icon {
  position: absolute;
  right: 1%;
  top: 3%;
  color: white;
  font-size: 2rem;
  z-index: 999;
}

.video-overlay {
  position: fixed;
  left: 6%;
  top: 3vh;
  border-radius: 15px;
  width: 88%;
  height: 94vh;
  background-color: rgba(0, 0, 0, 0.541);
  z-index: 54;
}

/* hamburger  */

.ham {
  cursor: pointer;
  -webkit-tap-highlight-color: transparent;
  transition: transform 400ms;
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
  z-index: 999;
}

.line {
  fill: none;
  transition: stroke-dasharray 400ms, stroke-dashoffset 400ms;
  stroke: var(--green);
  stroke-width: 2;
  stroke-linecap: round;
}

.ham3 .top {
  stroke-dasharray: 40 130;
}
.ham3 .middle {
  stroke-dasharray: 40 140;
}
.ham3 .bottom {
  stroke-dasharray: 40 205;
}
.ham3.active .top {
  stroke-dasharray: 75 130;
  stroke-dashoffset: -63px;
}
.ham3.active .middle {
  stroke-dashoffset: -102px;
}
.ham3.active .bottom {
  stroke-dasharray: 110 205;
  stroke-dashoffset: -86px;
}
/* MENU  */

.menu {
  position: fixed;
  right: 0;
  width: 100%;
  z-index: 999;
  height: 100vh;
  background-color: #070708;
}
nav {
  position: relative;
  width: 88%;
  margin: 0 auto;
  height: 100%;
  /* background: var(--selectionColor); */
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}
nav ul {
  /* padding-left: 2rem; */
  list-style: none;
  font-family: var(--titleFont);
}
nav ul li {
  position: relative;
  font-size: 6rem;
  font-weight: 700;
  color: var(--green);
  overflow: hidden;
}
.link-layer {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background: #070708;
}
.svgCurve {
  position: fixed;
  top: 0;
  /* left: 20%; */
  right: 100%;
  width: 100px;
  height: 100%;
  fill: #070708;
  stroke: none;
}
/* Gallery  */
.gallery {
  height: 175vh;
  background: var(--bg);
  display: flex;
  flex-direction: row;
  gap: 2vw;
  /* padding: 2vw; */
  overflow: hidden;
}
.gallery-column {
  width: 25%;
  height: 100%;
  display: flex;
  flex-direction: column;
  /* gap: 2vw; */
  position: relative;
}
.gallery-column:nth-of-type(1) {
  top: -45%;
}
.gallery-column:nth-of-type(2) {
  top: -90%;
}
.gallery-column:nth-of-type(3) {
  top: -45%;
}
.gallery-column:nth-of-type(4) {
  top: -75%;
}
.gallery-image-container {
  width: 100%;
  height: 100%;
  position: relative;
  overflow: hidden;
  border-radius: 1vw;
}
.gallery-image-container img {
  object-fit: cover;
  width: 340px;
  height: 460px;
  border-radius: 1vw;
}
.spacer {
  height: 100vh;
  background: var(--bg);
}
/* METHODS  */
.methods {
  position: relative;
  height: 110vh;
  width: 100%;
  margin-top: 60px;
}
.section-title {
  height: 12vh;
  display: flex;
  align-items: center;
  font-size: 5rem;
  font-family: var(--titleFont);
  color: var(--textColor);
  text-transform: uppercase;
}
.paragraph {
  /* font-size: 5rem; */
  font-family: var(--font);
  /* text-transform: uppercase; */
  max-width: 1200px;
  display: flex;
  flex-wrap: wrap;
  line-height: 1;
}
.word {
  margin-right: 12px;
  margin-top: 12px;
  position: relative;
}
.word-shadow {
  position: absolute;
  opacity: 0.1;
}
.methods-content {
  height: 88vh;
  width: 100%;
  /* background: orange; */
  display: flex;
}
.methods-left-container {
  width: 50%;
}
.methods-left-container ul {
  height: 40%;
  /* background: red; */
  display: flex;
  justify-content: center;
  /* align-items: center; */
  flex-direction: column;
  list-style: none;
}
.method-description {
  height: 60%;
  /* background: blue; */
  color: white;
  font-family: var(--font);
  font-size: 1.3rem;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
.methods-left-container li {
  font-size: 3rem;
  font-family: var(--font);
  margin-top: 20px;
  color: var(--textColor);
}
.methods-right-container {
  width: 50%;
  height: 100%;
  display: flex;
  /* background: red; */
  justify-content: flex-end;
  align-items: center;
}
.methods-right-container img {
  width: 60%;
  height: 84%;
  object-fit: cover;
  border-radius: 2vw;
}
/* media queries  */
@media (max-width: 1450px) {
  .video-text .title {
    font-size: 5rem;
  }
}
@media (max-width: 1240px) {
  .video-text .title {
    font-size: 4rem;
  }
  .video-text {
    font-size: 1.3rem;
  }
}
@media (max-width: 1000px) {
  .video-text .title {
    font-size: 3.5rem;
  }
}
@media (max-width: 860px) {
  .hero-primary-text h1:first-child {
    font-size: 4.5rem;
  }
  .hero-primary-text h1:nth-child(2) {
    font-size: 2.66rem;
  }
  .hero-primary-text {
    line-height: 50px;
  }
  .hero-secondary-text {
    font-size: 0.92rem;
  }
  .video-text .title {
    font-size: 3rem;
  }
  .video-text {
    font-size: 1.2rem;
  }
}
@media (max-width: 768px) {
  main {
    width: 96%;
  }
  header {
    width: 96%;
  }
  .video-overlay {
    width: 96%;
    left: 2%;
  }
}
@media (max-width: 734px) {
  /* .video-text {
    width: 550px;
  } */
  .video-text .title {
    font-size: 2.7rem;
  }
  .video-text {
    font-size: 1.1rem;
  }
}
@media (max-width: 712px) {
  .hero-primary-text h1:first-child {
    font-size: 4rem;
  }
  .hero-primary-text h1:nth-child(2) {
    font-size: 2.36rem;
  }
  .hero-primary-text {
    line-height: 45px;
  }
}
@media (max-width: 610px) {
  .video-text .title {
    font-size: 2.4rem;
  }
  .video-text {
    font-size: 1rem;
  }
}
@media (max-width: 600px) {
  .hero-primary-text h1:first-child {
    font-size: 3.5rem;
  }
  .hero-primary-text h1:nth-child(2) {
    font-size: 2.08rem;
  }
  .hero-primary-text {
    line-height: 40px;
  }
}
@media (max-width: 500px) {
  .hero-primary-text h1:first-child {
    font-size: 3rem;
  }
  .hero-primary-text h1:nth-child(2) {
    font-size: 1.79rem;
  }
  .hero-primary-text {
    line-height: 34px;
  }
  .hero-secondary-text {
    font-size: 1rem;
  }
}
@media (max-width: 440px) {
  .hero-primary-text h1:first-child {
    font-size: 2.8rem;
  }
  .hero-primary-text h1:nth-child(2) {
    font-size: 1.67rem;
  }
  .hero-primary-text {
    line-height: 30px;
  }
}

@media (max-width: 395px) {
  .hero-primary-text h1:first-child {
    font-size: 2.6rem;
  }
  .hero-primary-text h1:nth-child(2) {
    font-size: 1.55rem;
  }
  .hero-secondary-text {
    font-size: 0.9rem;
  }
  .hero-primary-text {
    line-height: 30px;
  }
}
